.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: -10rem;
  position: relative;
  background-image: linear-gradient(to right, rgba(255,0,0,0), rgba(255,0,0,1));
  color: white;
  border-radius: 14px;
  padding: 1rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
 
}

.summary h2 {
  font-size: 2rem;
  margin-top: 0;
}
